<template>
  <div class="jd-verify-code-small">
    <input type="text" placeholder="6"
      v-model="code">
    <div class="operate">
      <span class="spare-time" v-if="start">{{time}}S</span>
      <a href="javascript:;" @click="getCode" v-else></a>
    </div>
  </div>
</template>
<script>
/**
 * @file components/common/VerifyCodeSmall.vue
 * @description 
 * @createTime 2019040814:44:09
 */
import { INPUT_TYPE_VERIFY_CODE_SPARE_TIME } from 'vue-jd-ui/src/utils/constant'

export default {
  name: 'JdVerifyCodeSmall',
  data() {
    return {
      // 
      code: '',
      // 
      time: INPUT_TYPE_VERIFY_CODE_SPARE_TIME,
      // timer
      timeTimer: null,
      // 
      start: false
    }
  },
  methods: {
    // 
    getCode() {
      if (this.timeTimer) {
        return
      }
      this.start = true
      this.timeTimer = setInterval(() => {
        this.time--
        if (this.time <= 0) {
          this.reset()
        }
      }, 1e3)
    },
    reset() {
      clearInterval(this.timeTimer)
      this.timeTimer = null
      this.time = INPUT_TYPE_VERIFY_CODE_SPARE_TIME
      this.start = false
    }
  },
  destroyed() {
    this.reset()
  }
}
</script>
