<template>
  <div class="jd-input-box">
    <div v-if="isSubmit" class="input-submit">
      <div class="input">
        <input
          type="text"
          :value="value"
          @change="changeHandle"
          @input="changeHandle">
        <span v-show="showPlaceholder" class="placeholder">{{placeholder}}</span>
        <button class="all-in" @click="allInHandle">{{allInText}}</button>
      </div>
      <button class="confirm" @click="confirmHandle">{{confirmText}}</button>
      <p class="format"> {{toChinese}}</p>
      <p class="warning" v-show="warning.show">{{warning.text}}</p>
    </div>
    <div v-else class="input-input">
      <div class="input">
        <input
          type="text"
          :value="value"
          @change="changeHandle"
          @input="changeHandle"
          @dblclick="dblclickHandle">
        <span v-show="showPlaceholder" class="placeholder">{{placeholder}}</span>
      </div>
      <p class="format" v-show="value">{{toChinese}}</p>
      <p class="format" v-show="!value">{{transformText}}</p>
      <p class="warning" v-show="warning.show">{{warning.text}}</p>
    </div>
  </div>
</template>
<script>
/**
 * @prop { String / Number: required } value 
 * @prop { String } type 
 * @prop { String } placeholder
 * @prop { String } allInText  typesubmit
 * @prop { String } confirmText  typesubmit
 * @prop { String } transformText 
 * @prop { Object } warning  @property { Boolean } show  @property { String } text 
 * @event all-in-handle   typesubmit
 * @event confirm-handle   typesubmit
 * @event double-handle   typeinput
 * @event change-handle input
 */
import { numberToChinese } from 'vue-jd-ui/src/utils/tools'
import { BUTTON_SUBMIT } from 'vue-jd-ui/src/utils/constant'
export default {
  name: 'JdInput',
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    type: {
      type: String,
      default: BUTTON_SUBMIT // typeconstant.js
    },
    placeholder: {
      type: String,
      default: ''
    },
    // 
    allInText: {
      type: String,
      default: ''
    },
    // 
    confirmText: {
      type: String,
      default: ''
    },
    transformText: {
      type: String,
      default: ''
    },
    // 
    warning: {
      type: Object,
      default: () => ({
        show: false,
        text: ''
      })
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showPlaceholder: true
    }
  },
  watch: {
    value(val) {
      if (!String(val)) {
        this.showPlaceholder = true
      }
      else {
        this.showPlaceholder = false
      }
    }
  },
  computed: {
    toChinese() {
      if (!isNaN(this.value)) {
        return numberToChinese(this.value)
      }
      return ''
    },
    isSubmit() {
      return this.type === BUTTON_SUBMIT
    }
  },
  mounted() {
    if (!String(this.value)) {
      this.showPlaceholder = true
    }
    else {
      this.showPlaceholder = false
    }
  },
  methods: {
    changeHandle(e) {
      const value = e.target.value
      if (!String(value)) {
        this.showPlaceholder = true
      }
      else {
        this.showPlaceholder = false
      }
      const noSpaceValue = String(value).trim()
      this.$emit('input', noSpaceValue)
      this.$emit('change-handle', noSpaceValue)
    },
    allInHandle() {
      this.$emit('all-in-handle')
    },
    confirmHandle() {
      this.$emit('confirm-handle')
    },
    dblclickHandle() {
      this.$emit('double-handle')
    }
  }
}
</script>
