<template>
  <div class="jd-step-bar">
    <ul v-if="!isProfit" class="normal">
      <li v-for="(item, i) in list" :key="i" :class="{step: item.step}">
        <div class="name">{{item.name}}</div>
        <div class="dot"><i>{{item.step}}</i></div>
        <div class="time">{{item.time}}</div>
      </li>
    </ul>
    <ul v-else class="profit">
      <li v-for="(item, i) in list" :key="i" :class="{'amount-box' : isAmount(item.profit)}">
        <div class="name">{{item.name}}</div>
        <div :class="isAmount(item.profit) ? 'amount' : 'dot'"><i>{{item.profit}}{{isAmount(item.profit) ? '' : ''}}</i></div>
        <div class="time">{{item.time}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
/**
 * @prop { Array } list
 *    @property { String: required } name 
 *    @property { String: required } time 
 *    @property { Number } step  type  'normal'
 *    @property { String } profit  type  'profit'
 * @prop { String } type  'normal'() 'profit'()
 */
export default {
  name: 'JdStepBar',
  props: {
    list: {
      type: Array,
      default: () => []
    },
    type: {
      type: String,
      default: 'normal'
    }
  },
  computed: {
    isProfit() {
      return this.type === 'profit'
    }
  },
  methods: {
    isAmount(profit) {
      return profit
    }
  }
}
</script>
