<template>
  <div class="jd-select-box">
    <el-select
      class="select"
      size="size"
      placeholder=""
      :value="value"
      @change="changeHandle"
      @clear="clearHandle">
      <el-option
        v-for="(item, i) in selectOptions"
        :key="i"
        :label="item.label"
        :value="item.value"></el-option>
    </el-select>
  </div>
</template>
<script>
/**
 * @name jd-select
 * @description 
 * @param { String } value 
 * @param { Array } selectOptions 
 */
import elSelect from '../el-select'
import elOption from '../el-option'

export default {
  name: 'JdSelect',
  components: {
    elSelect,
    elOption
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    selectOptions: {
      type: Array,
      default: () => ([])
    }
  },
  methods: {
    changeHandle(val) {
      this.$emit('input', val)
    },
    clearHandle() {
      this.$emit('input', '')
    }
  }
}
</script>
