<template>
  <div class="jd-table"
    :class="{'frist-at-one': firstAtOne}">
    <el-table stripe
      :data="tableData"
      :class="hasFilter"
      :show-header="showHeader">
      <el-table-column
        v-for="(column, i) in columns"
        :key="i"
        :label="column.label"
        :prop="column.prop"
        :width="column.width">
          <template slot="header" slot-scope="scope">
            <slot :name="column.filter" :column="scope.column">
              <span class="table-head" :class="{'table-head-padding' : i === 0}">{{scope.column.label}}</span>
            </slot>
          </template>
          <template slot-scope="scope" >
            <div
              v-if="column.render"
              v-html="column.render(scope)"
              :class="{'table-head-padding' : i === 0}"></div>
            <template v-else-if="column.slot" >
              <slot :name="column.slot" :row="scope.row"></slot>
            </template>
            <div v-else>
              <span class="normal-content">{{scope.row[column.prop]}}</span>
            </div>
          </template>
        </el-table-column>
    </el-table>
    <el-pagination
      class="pagination"
      :page-size="pageSize"
      :pager-count="5"
      :current-page="currentPage"
      @current-change="currentChange"
      background
      layout="->, total, slot, prev, pager, next, jumper"
      :total="total"
      v-if="isShowPage">
      <span class="size">{{totalPage}} {{pageSize}}/</span>
    </el-pagination>
  </div>
</template>
<script>
/**
 * @event current-change 
 * @prop { Array } tableData 
 * @prop { Boolean } isShowPage 
 * @prop { Boolean } firstAtOne 
 * @prop { Boolean } showHeader 
 * @prop { Number } currentPage 
 * @prop { Number } total 
 * @prop { Number } pageSize 
 * @prop { Array } columns 
 * @extends columns
 *    @property { String } label 
 *    @property { String } prop 
 *    @property { [String, Number] } width 
 *    @property { Function(scope) } render 
 *    @property { String } slot html slotslotname @augments { Object } row 
 *    @property { String } filter filterslotname@augments { Object } slotProps 
 */
import elTable from '../el-table'
import elTableColumn from '../el-table-column'
import elPagination from '../el-pagination'

export default {
  name: 'JdTable',
  components: {
    elTable,
    elTableColumn,
    elPagination
  },
  props: {
    // 
    tableData: {
      type: Array,
      default: () => ([])
    },
    columns: {
      type: Array,
      default: () => ([])
    },
    // 
    currentPage: {
      type: Number,
      default: 1
    },
    // 
    pageSize: {
      type: Number,
      default: 10
    },
    // 
    total: {
      type: Number,
      default: 1
    },
    // 
    isShowPage: {
      type: Boolean,
      default: true
    },
    // ()
    firstAtOne: {
      type: Boolean,
      default: false
    },
    // 
    showHeader: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    // 
    hasFilter() {
      const filterArray = this.columns.filter(el => {
        return el.filter
      })
      if (filterArray.length !== 0) {
        return 'header-filter'
      }
      return ''
    },
    // 
    totalPage() {
      const { total, pageSize } = this
      return Math.ceil(total / pageSize) || 1
    }
  },
  methods: {
    // expand
    rowClassName({ row, rowIndex }) {
      if (this.firstAtOne) {
        return rowIndex % 2 === 0 ? 'table-active' : ''
      }
      else {
        return ''
      }
    },
    // 
    currentChange(page) {
      this.$emit('current-change', page)
    }
  }
}
</script>
