<template>
  <ul class="jd-step"
    :class="`${type}`">
    <li v-for="(step, key) in cSteps"
      :key="key"
      :class="{
        'active': step.active,
        'active-last': step.isActiveLast
      }"
      @click="stepClick">
      <div class="j-s-circle">
        <i>{{step.index}}</i>
      </div>
      <span class="line"></span>
      <div class="j-s-info">
        <jd-scale :text="step.text" :multiple="10/12" v-if="!step.active"></jd-scale>
        <span v-else>{{step.text}}</span>
      </div>
    </li>
  </ul>
</template>
<script>
/**
 * @file components/common/Step.vue
 * @description 
 * @createTime 2019032515:00:37
 */
import JdScale from '../scale'

export default {
  name: 'JdStep',
  components: {
    JdScale
  },
  props: {
    /**
     * @desc
     * :
     *  small 
     *  middle 
     */
    type: {
      type: String,
      default: 'small'
    },
    // 
    steps: {
      type: Array,
      default() {
        return []
      }
    },
    // step
    step: {
      type: Number,
      default: 0
    }
  },
  computed: {
    cSteps() {
      return this.steps.map((step, index) => {
        step.active = step.index <= this.step
        step.isActiveLast = step.index === this.step
        return step
      })
    }
  },
  methods: {
    stepClick() {

    }
  }
}
</script>
