<template>
  <div class="jd-verify-code">
    <div class="input-box clearfix">
      <div class="input">
        <input type="text"
          :value="value"
          @input="changeHandle"
          @change="changeHandle">
        <span v-show="showPlaceholder" class="placeholder">{{placeholder}}</span>
      </div>
      <div
        class="send"
        :class="{forbidden: lock}"
        @click="sendHandle">{{sendText}}</div>
    </div>
    <div v-show="warning" class="warn">
      <div ref="slot">
        <slot name="warn">
          <p>{{ phone | mask-phone }}
            <span class="voice" @click="voiceHandle"></span>
          </p>
        </slot>
      </div>

    </div>
  </div>
</template>
<script>
/**
 * @prop { Number / String: required } value code
 * @prop { String: required } phone 
 * @prop { Boolean } isCs 
 * @prop { String } type ,  isCs: false 
 * @prop { String } placeholder placeholder
 * @prop { Number } time 
 * @prop { Function } codePromise 
 * @event send-handle 
 * @event get-certificate 
 * @template slot:warn 
 * @function reset ref
 */
import { PUSH_TXT, PUSH_VOICE } from 'vue-jd-ui/src/utils/constant'
import ElMessage from 'vue-jd-ui/packages/el-message'

export default {
  name: 'JdVerifyCode',
  props: {
    value: {
      type: [Number, String],
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    time: {
      type: Number,
      default: 30
    },
    codePromise: {
      type: Function,
      default: (params) => {
        return new Promise((resolve, reject) => {
          reject(new Error('Promise'))
        })
      },
      required: true
    },
    phone: {
      type: String,
      default: '******',
      required: true
    },
    isCs: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      $message: ElMessage,
      // 
      sendText: '',
      // 
      lock: false,
      // 
      voiceLock: false,
      // 
      timer: null,
      // 
      currentTime: this.time,
      // warn
      warning: false
      // codePromise: this.$api.smsPush
      // csCodePromise: new Promise()
    }
  },
  computed: {
    showPlaceholder() {
      if (!String(this.value)) {
        return true
      }
      else {
        return false
      }
    }
    // codePromise() {
    //   if (this.isCs) {
    //     return new Promise((resolve, reject) => {
    //       resolve()
    //     })
    //   }
    //   else {
    //     return this.$api.smsPush
    //   }
    // }
  },
  destroyed() {
    this.clearCountDown()
  },
  methods: {
    // 
    changeHandle(e) {
      let value = e.target.value
      value = String(value).trim()
      this.$emit('input', value)
    },
    // 
    syncCertificate(traceId) {
      this.$emit('get-certificate', traceId)
    },
    // 
    sendHandle() {
      if (this.lock || this.timer) {
        return false
      }
      this.lock = true
      this.$emit('send-handle')
      if (this.isCs) {
        // this.csCodePromise()
      }
      else {
        this.codePromise({
          pushType: PUSH_TXT,
          phoneNo: this.phone,
          action: this.type
        }).then(({ data }) => {
          const { traceId, message } = data
          this.warning = true
          this.$message.success(message)
          this.syncCertificate(traceId)
          // 
          this.countDown()
        }, reject => {
          console.error(reject)
          // 
          this.clearCountDown()
        })
      }
    },
    // 
    voiceHandle() {
      if (this.voiceLock) {
        return false
      }
      this.voiceLock = true
      if (this.isCs) {
        // this.csCodePromise()
      }
      else {
        this.codePromise({
          pushType: PUSH_VOICE,
          phoneNo: this.phone,
          action: this.type
        }).then(({ data }) => {
          console.log('send voice')
          const { traceId, message } = data
          this.$message.success(message)
          this.voiceLock = false
          this.syncCertificate(traceId)
        }, reject => {
          console.error(reject)
          this.voiceLock = false
        })
      }
    },
    // 
    countDown() {
      this.sendText = this.time + 's'
      this.timer = setInterval(() => {
        // 
        this.currentTime = this.currentTime - 1
        // 
        this.sendText = this.currentTime + 's'
        if (this.currentTime === 0) {
          // 
          this.clearCountDown()
        }
      }, 1000)
    },
    // 
    clearCountDown() {
      clearInterval(this.timer)
      this.timer = null
      this.sendText = ''
      // lock
      this.lock = false
      // 
      this.currentTime = this.time
    },
    // reset
    reset() {
      this.warning = false
      this.$emit('input', '')
      this.$emit('get-certificate', '')
      this.clearCountDown()
    }
  }
}
</script>
